<template>
	<div>
		<section class="header" v-if="loggedIn">
			<b-navbar toggleable="xl" type="dark">
				<b-navbar-brand class="mt-n1" to="/"
					><b-icon-server /> {{ $appsettings.details.title }}</b-navbar-brand
				>
				<b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
				<b-collapse id="nav-collapse" is-nav>
					<b-navbar-nav>
						<b-nav-item
							v-for="(_module, modname) in modules"
							:key="modname"
							:to="'/' + modname"
							:exact="modname == '/' ? true : false"
						>
							<b-icon v-if="_module.icon" :icon="_module.icon"></b-icon>
							{{
								(localeEl.nav_items[ _module.home ])
								||
								_module.caption
								| capitalize
							}}
							<b-navbar-nav class="d-block d-xl-none">
								<b-nav-item
									v-for="(view, viewname) in _module['views']"
									:key="viewname"
									:to="'/' + currentmodule + '/' + viewname"
								>
									- 
									{{
										localeEl.nav_child_items[currentmodule][
											viewname
										]
										|| 
										view.caption
									}}

								</b-nav-item>
							</b-navbar-nav>
						</b-nav-item>
					</b-navbar-nav>

					<b-navbar-nav class="ml-auto">
						<!-- <b-nav-item
                     v-for="(view, viewname) in modules[currentmodule]['views']"
                    :key="viewname"
                    :to="'/'+currentmodule +'/'+ viewname"
                  >
                    {{view.caption}}
                  </b-nav-item> -->
						<b-nav-text class="py-0 mr-3">
							<SearchBar :modules="modules" :menuList="menuList" />
						</b-nav-text>

						<MegaMenu :menuList="menuList" v-if="$config.MEGAMENU" />

						<b-nav-item title="Change Theme" id="change_theme">
							<b-icon-brush class="icon d-none d-xl-inline" />
							<span class="d-inline-block d-xl-none">Change Theme</span>
						</b-nav-item>

						<b-popover
							custom-class="change_theme"
							target="change_theme"
							triggers="hover"
							placement="top"
						>
							<b-button
								variant="link"
								@click="toggleTheme('primary')"
								class="primary"
							></b-button>
							<b-button
								variant="link"
								@click="toggleTheme('secondary')"
								class="secondary"
							></b-button>
							<b-button
								variant="link"
								@click="toggleTheme('success')"
								class="success"
							></b-button>
							<b-button
								variant="link"
								@click="toggleTheme('danger')"
								class="danger"
							></b-button>
							<b-button
								variant="link"
								@click="toggleTheme('warning')"
								class="warning"
							></b-button>
							<b-button
								variant="link"
								@click="toggleTheme('info')"
								class="info"
							></b-button>
							<b-button
								variant="link"
								@click="toggleTheme('dark')"
								class="dark"
							></b-button>
						</b-popover>

						<!-- <b-nav-item title="Report Manager">
							<b-icon-card-checklist class="icon d-none d-xl-inline" />
							<span class="d-inline-block d-xl-none">Report Manager</span>
						</b-nav-item> -->
						
						<b-popover target="nav-item-profile" triggers="hover" placement="top">
							<b-avatar class="mr-2" variant="info" :text="user.initials"></b-avatar>
							{{ user.name || "User" | capitalize }}
						</b-popover>

						<BaseNotification
							:configurl="$config.NREST"
							:connect_room="$config.NCollection"
							:_userid="session_userid"
							:_ticket="session_ticket"
							v-if="$config.NSERVER"
						/>



						<b-nav-item id="nav-item-profile">
							<b-icon-person-plus class="icon d-none d-xl-inline" />
							<span class="d-inline-block d-xl-none">User</span>
						</b-nav-item>

						
						<b-nav-item title="Logout" @click="logout('Logout')">
							<b-icon-box-arrow-in-right class="icon d-none d-xl-inline" />
							<span class="d-inline-block d-xl-none">Logout</span>
						</b-nav-item>
					</b-navbar-nav>
				</b-collapse>
			</b-navbar>
			<b-navbar class="subnav d-none d-xl-block" type="light">
				<b-navbar-nav>
					<b-nav-item
						v-for="(view, viewname) in modules[currentmodule]['views']"
						:key="viewname"
						:to="'/' + currentmodule + '/' + viewname"
					>
						{{
							localeEl.nav_child_items[currentmodule][
								viewname
							]
							|| 
							view.caption
						}}
					</b-nav-item>
				</b-navbar-nav>
			</b-navbar>
		</section>
	</div>
</template>

<script>
import MegaMenu from "./mega_menu";
import SearchBar from "./SearchBar/SearchBar";

export default {
	components: { MegaMenu, SearchBar },
	data() {
		var modules = JSON.parse(JSON.stringify(this.$appsettings.modules));
		var loggedIn = this.$credCAPI.loggedIn;
		return {
			localeName: "app",
			modules: modules,
			loggedIn: loggedIn,
			currentmodule: this.$appsettings.details.home || "",
			user: {
				name: sessionStorage.getItem("_userid"),
			},
			menuList: [],
		};
	},
	computed: {
		localeEl() {
			return this.$t(this.localeName);
		},
	},
	created() {
		if(this.$config.MEGAMENU){
			this.getMenuList();
		}
	},
	mounted() {
		this.$credCAPI.subscribe("login", () => {
			this.loggedIn = true;
			this.user = JSON.parse(JSON.stringify(this.$credCAPI.user));
		});
		this.$credCAPI.subscribe("logout", () => (this.loggedIn = false));
		this.toggleTheme(localStorage.getItem("panelTheme"));
	},
	watch: {
		$route(to, from) {
			this.currentmodule = to.matched[0].meta.module;
		},
	},
	methods: {
		session_userid() {
			return sessionStorage.getItem("_userid");
		},
		session_ticket() {
			return sessionStorage.getItem("_ticket");
		},
		logout(action) {
			this.$_confirmMessage({ action: action })
				.then((value) => {
					if (value) {
						this.$credCAPI.logout();
						// this.$router.push("/");
						location.href = "/";
					} else {
						this.$store.commit("loading", false);
					}
				})
				.catch((err) => {
					console.log(err);
				});
		},
		loadModule(module) {
			this.currentmodule = module;
			this.$router.push(module.path);
		},
		toggleTheme(theme) {
			//  if(document.documentElement.hasAttribute('theme')){
			//       document.documentElement.removeAttribute('theme');
			//     }
			//     else{
			//       document.documentElement.setAttribute('theme', theme);
			//     }
			document.documentElement.setAttribute("theme", theme);
			localStorage.setItem("panelTheme", theme);
		},
		getMenuList() {
			var self = this;
			self.$credCAPI
				.collection("apps_role/read")
				.read({})
				.then((response) => {
					self.menuList = response.appdata;
					this.$store.commit("loading", false);
				})
				.catch((error) => console.error(error));
		},
	},
};
</script>


<style lang="scss" scoped>
.header {
	.navbar-dark {
		background: var(--blue-dark);
		.nav-item {
			padding-right: 0.2rem;
			padding-left: 0.2rem;
		}
		.nav-link {
			color: rgb(255 255 255 / 70%);
			// display: flex;
			.navbar-nav {
				padding: 0 1rem;
			}
			.b-icon {
				margin: 0 5px;
			}
			&:focus,
			&.router-link-active {
				color: var(--white);
			}
			img {
				height: 15px;
				margin: 0 5px;
			}
			.icon {
				width: 18px;
				height: 18px;
			}
		}
	}
	.navbar-light.subnav {
		font-size: 1rem;
		font-weight: 600;
		padding: 0;
		background: #fff;
		box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
		.nav-link {
			position: relative;
			padding-left: 1rem;
			padding-right: 1rem;
			&::after {
				content: "";
				position: absolute;
				bottom: 0;
				left: 50%;
				width: 0;
				height: 1px;
				background: #3d448e;
				transition: width 0.4s ease-in-out;
				transform: translateX(-50%);
			}
			&:focus,
			&.router-link-active {
				color: #3d448e;
			}
			&.router-link-active::after {
				width: 100%;
			}
		}
	}
}
</style>