import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);


function getRouteComponents(routes) {
  if (routes.component && typeof routes.component == "string")
    routes.component = viewComps[routes.component];

  if (routes instanceof  Array) {
    for (var i = 0; i < routes.length; i++) {
      getRouteComponents(routes[i]);
    }
  }
  if (routes.children) {
    for (var i = 0; i < routes.children.length; i++) {
      getRouteComponents(routes.children[i]);
    }
  }
}

import { settings, componentlist, routes } from "../appsettings";
// import {  appsettings } from "../appsettings";

const viewComps = componentlist;
const appsettings = settings;
var appRoutes = routes;

getRouteComponents(appRoutes);

import login from "../components/login";
appRoutes.push({ path: "/login", component: login });

const router = new VueRouter({ mode: "hash", routes: appRoutes });

router.beforeEach((to, from, next) => {
  if (to.path !== "/login" && !router.app.$credCAPI.loggedIn)
    next({ path: "/login" });
  else next();
});
export default router;
