import Vue from "vue";
Vue.config.productionTip = false;

import store from "@/store";
import App from "@/App.vue";
import router from "@/router";

/*CONFIG */
Vue.prototype.$config = Vue['$config'] = window.config;
console.log(Vue.prototype.$config)

/*FONT AWESOME*/
import "@fortawesome/fontawesome-free/js/fontawesome";
import "@fortawesome/fontawesome-free/js/solid";
import "@fortawesome/fontawesome-free/js/regular";
import "@fortawesome/fontawesome-free/js/brands";

/*BOOTSTRAP*/
import { BootstrapVue, IconsPlugin } from "bootstrap-vue";
Vue.use(BootstrapVue);
Vue.use(IconsPlugin);
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";

/*APP STYLES*/
import "@/assets/css/common.scss";
import "@/assets/css/style.scss";

/*APP SETTING*/
import { settings } from "@/appsettings";
Vue.prototype.$appsettings = settings;

/*  Macro */
import { Macro } from "credcomponents";
Vue.use(Macro, { applicationName: "iwebz-etf", macroJSFileUrl: process.env.NODE_ENV === 'development' ? "js/macros/macro.js" : config.macronginxurlprefix + "/macros/macro.js" });

/*REST SETTING*/
const servertype = config.jsonserver ? 'jsonserver' : 'apiserver'
const baseurl = config.jsonserver ? config.JREST : config.NREST
import credCAPI from "@/utils/credCAPI";
window.credCAPI = Vue.$credCAPI = Vue.prototype.$credCAPI = credCAPI({ baseurl: baseurl, servertype: servertype });
window.app_process = process.env.NODE_ENV === 'development' ? "development" : "production";
/* Plugins */
import Plugin from '@/plugins';
Vue.use(Plugin, store);


/* layout */
import dashboard from "@/components/layout"
Vue.use(dashboard)

import { i18n, loadLanguageAsync } from './i18n';
router.beforeEach((to, from, next) => {
  const lang = config.applanguage
  loadLanguageAsync(lang).then(() => next())
});


new Vue({
  i18n,
  router: router,
  store: store,
  render: (h) => h(App),
}).$mount("#app");
