<template>
	<div class="modal-route">
		{{ requiredFieldsText }}
		<div class="modal in" style="opacity: 1; display: block">
			<div
				class="modal-dialog modal-dialog-centered modal-dialog-scrollable"
				role="document"
				:class="`modal-${size}`"
			>
				<div class="modal-content">
					<div class="modal-header">
						<input id="modal_focus" type="hidden" />
						<h4 v-if="!hasHeaderSlot()" class="mb-0">{{ title }}</h4>
						<div v-else>
							<slot name="header"></slot>
						</div>

						<button
							@click="handleClose()"
							type="button"
							class="close"
							data-dismiss="modal"
							aria-label="Close"
						>
							<span aria-hidden="true">&times;</span>
						</button>
					</div>
					<div :class="noPadding?'':'modal-body'"> 
						<slot name="body"></slot>
					</div>
					<div class="modal-footer">
						<small
							v-if="hasFooterSlot() && requiredFieldsText"
							class="text-muted mr-auto"
							><span class="text-danger">*</span> Required Fields</small
						>
						<button
							@click="handleClose"
							type="reset"
							class="btn btn-outline-secondary"
							data-dismiss="modal"
						>
							Close
						</button>
						<slot name="footer"></slot>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	props: ["size", "title", "requiredFieldsText", "noPadding"],
	methods: {
		handleClose() {
			if (this.hasFooterSlot()) {
				if (this.$router.history.current.query.scope == "view") {
					this.$router.go(-1);
				} else {
					this.$_confirmMessage({
						size: "md",
						msg: "Are you sure you want to close? Unsaved data will be lost.",
					}).then((value) => {
						if (value) {
							this.$router.go(-1);
							this.$bvToast.hide("validation_toast");
						}
					});
				}
			} else {
				this.$router.go(-1);
			}
		},
		hasFooterSlot() {
			return !!this.$slots.footer;
		},
		hasHeaderSlot() {
			return !!this.$slots.header;
		},
	},
};
</script>

<style lang="scss" scoped>
.modal-route {
	position: fixed;
	top: 0;
	left: 0;
	height: 100vh;
	width: 100vw;
	background-color: rgba(21, 23, 35, 0.8);
	display: block;
	align-items: center;
	justify-content: center;
	z-index: 3;
}

.modal {
	.modal-body {
		padding-bottom: 80px;
	}
	.modal-content {
		border: 0;
		border-radius: 0;
	}
	// .modal-footer{border:0}
	.modal-footer {
		border: 0;
		background: #f7f7f7;
	}
	.modal-header {
		background: var(--blue-dark);
		color: var(--white);
		border-radius: 0;
	}
	.modal-header h4 {
		text-transform: capitalize;
	}
	.close {
		color: var(--white);
		opacity: 0.6;
	}
	button.close:focus {
		opacity: 1;
		outline: none !important;
	}
	.modal-xxl {
		max-width: 97%;
		.modal-body {
			// padding: 0;
		}
	}
}
</style>