<template>
	<v-select
		label="text"
		:filterable="false"
		:options="options"
		@search="onSearch"
		v-if="vshow"
		:disabled="vdisabled"
		v-bind="$attrs"
		v-on="inputListeners"
		:value="value"
		:placeholder="placeholder"
	>
		<template slot="no-options"> {{ placeholderText }} </template>
		<template slot="option" slot-scope="option">
			<div class="d-center">
				{{ option.text }}
			</div>
		</template>
		<template slot="selected-option">
			<div class="selected d-center">
				{{ selectedOption }}
			</div>
		</template>
	</v-select>
</template>

<script>
const _toString = Object.prototype.toString;
const getJSONdata = function (fieldlist, vm) {
	if (!vm.isExpression) return fieldlist;
	let regex = /\[([^\]]*)\]/g;
	if (!fieldlist) return [];
	let expression = fieldlist.match(regex);
	let expr = "",
		exprVal = "";
	if (expression) {
		for (var i = 0; i < expression.length; i++) {
			expr = expression[i].replace("[", "").replace("]", "");
			exprVal = $("#" + expr).val();
			fieldlist = fieldlist.replace(expression[i], exprVal);
		}
	}
	fieldlist = "[" + fieldlist + "]";
	return fieldlist;
};
function debounce(fn, delay) {
	var timeoutID = null;
	return function () {
		clearTimeout(timeoutID);
		var args = arguments;
		var that = this;
		timeoutID = setTimeout(function () {
			fn.apply(that, args);
		}, delay);
	};
}
function isUndef(v) {
	return v === undefined || v === null;
}
function isPlainObject(obj) {
	return _toString.call(obj) === "[object Object]";
}

export default {
	props: {
		value: null,
		page: {
			type: Object,
			default: function () {
				return {
					start: 0,
					limit: 30,
				};
			},
		},
		label: {
			type: String,
			default: "",
		},
		dataList: {
			type: String,
		},
		dsCode: {
			type: String,
			required: true,
		},
		dsName: {
			type: String,
			required: true,
		},
		placeholder: {
			type: String,
			default: "Select",
		},
		fieldlist: {
			type: String,
			default: "",
		},
		show: {
			type: [String, Boolean, Object],
			default: true,
		},
		disabled: {
			type: [String, Boolean, Object],
			default: false,
		},
		url: {
			type: String,
			default: "etf/datalist/remoteread",
		},
		filter: {
			type: Array,
			default: function () {
				return [];
			},
		},
		filterView: {
			type: Boolean,
			default: false,
		},
		isRemoteRead: {
			type: Boolean,
			default: false,
		},
	},
	data() {
		return {
			options: [],
			selectedOption: "",
			placeholderText: "Type to search",
		};
	},
	computed: {
		vshow() {
			return isUndef(this.show)
				? true
				: isPlainObject(this.show)
				? this.show[this.id]
				: this.show;
		},
		vdisabled() {
			return isUndef(this.disabled)
				? false
				: isPlainObject(this.disabled)
				? this.disabled[this.id]
				: this.disabled;
		},
		inputListeners: function () {
			var vm = this;
			return Object.assign({}, this.$listeners, {
				input: function (value) {
					vm.selectedOption = value == null ? "" : value.text;
					vm.$emit("input", value == null ? null : value.id);
					vm.$emit("getSelectedValue", value.text);
				},
			});
		},
	},
	watch: {
		value: {
			handler(newVal) {
				if (newVal && this.options.length === 0) {
					this.getTextValueForManuallyChangedLabel(newVal);
				} else if (!newVal && this.options.length > 0) {
					this.options.length = 0;
				}
			},
			immediate: true,
		},
	},
	methods: {
		getTextValueForManuallyChangedLabel(newVal) {
			const _this = this,
				body = {
					_ticket: sessionStorage.getItem("_ticket"),
					dataset: _this.dataList,
					codefield: _this.dsCode,
					namefield: _this.dsName,
					filter: [
						{
							field: _this.dsCode,
							value: newVal,
							asgn: "eq",
							type: "string",
						},
						..._this.filter,
					],
					ids: "" + newVal,
					prepopulate: true,
					page: _this.page,
					jsondata: "[" + _this.fieldlist + "]",
				};
			_this.$credCAPI
				.collection(_this.url)
				.read({ body: body })
				.then((json) => {

					const _data = _this.isRemoteRead ? json.data : json;
					_this.options = _data.map((item) => ({
						text: item[_this.dsName] || item.VALUE,
						id: item[_this.dsCode] || item.ID,
					}));
					_this.selectedOption = ((_this.options || [])[0] || {}).text || "";
				})
				.catch((e) => {});
		},
		onSearch(search, loading) {
			loading(true);
			this.search(loading, search, this);
		},
		search: debounce((loading, search, vm) => {
			// vm.placeholderText =
			// 	search.length < 3 ? "Enter atleast 3 charactors..." : "Type to search";
			// if (search.length < 3) {
			// 	vm.options.length = 0;
			// 	loading(false);
			// 	return;
			// }
			console.log(vm.filter)
			if (search == null || search == "") return loading(false);
			const authorisedFilter = vm.filterView ? [] : [{ field:"AUTHORIZATION_STATUS", value: "1", asgn: "eq", type: "string" }]
			const body = {
				filter: [
					{
						field: vm.dsName,
						value: search,
						asgn: "like",
						type: "string",
					},
					...authorisedFilter,
					...vm.filter,
				],
				page: vm.page,
			};

			const remotereadfilter = {
					dataset: vm.dataList,
					codefield: vm.dsCode,
					namefield: vm.dsName,
					jsondata: JSON.stringify([{'field':vm.dsName, 'value':'', 'asgn':'like'}] || []),
					query:search,
			};
		
			vm.$credCAPI
				.collection(vm.url)
				.read(vm.isRemoteRead ? { body: remotereadfilter} : { body: body })
				.then((json) => {

					// let authoriseFilter = [];

					// if (vm.filterView) {
					// 	authoriseFilter = json;
					// } else {
					// 	const _data = vm.isRemoteRead ? json.data : json;
					// 	const arrayUniqueByKey = [
					// 		...new Map(_data.map((a) => [a[vm.dsCode], a])).values(),
					// 	];
					// 	// authoriseFilter = arrayUniqueByKey.filter(b => b.AUTHORIZATION_STATUS == "1");
					// 	authoriseFilter = arrayUniqueByKey;
					// }
					const _data = vm.isRemoteRead ? json.data : json;
					const arrayUniqueByKey = [
						...new Map(_data.map((a) => [a[vm.dsCode], a])).values(),
					];
					vm.options = arrayUniqueByKey.map((item) => ({
						text: item[vm.dsName] || item.VALUE,
						id: item[vm.dsCode] || item.ID,
					}));

					loading(false);
				})
				.catch((e) => loading(false));
		}, 350),
	},
};
</script>