<template>
	<section class="top_bar">
		<div v-if="filterView" class="filterOverlay" @click="filterViewHide()"></div>
		<b-navbar toggleable="xl">
			<b-navbar-brand v-if="localeEl.PageTitle">{{ PageTitle || localeEl.PageTitle }}</b-navbar-brand>
			<b-navbar-toggle target="nav-text-collapse">
				<template v-slot:default="{ expanded }">
					<b-icon v-if="expanded" icon="chevron-bar-up"></b-icon>
					<b-icon v-else icon="chevron-bar-down"></b-icon>
				</template>
			</b-navbar-toggle>

			<b-collapse id="nav-text-collapse" is-nav>
				<b-navbar-nav class="ml-auto">

					<slot name="body-start"></slot>

					<!-- <b-nav-text v-if="Object.keys(filterEl).length">
							<b-icon-arrow-counterclockwise class="icon" title="Clear Filters" />
					</b-nav-text> -->
					<macro-component
						:route="`/filter${$route.path}`"
						:setTargetThisRefence="$parent.$parent.getThisReference || getThisReference"
						v-if="$config.MACRO"
					/>


					<b-nav-text class="pt-2" v-if="isCheckbox">
						<button
							class="btn btn-default active"
							title="Clear Filters"
						>
							<b-form-checkbox v-model="selectAll" @input="selectAllCards()">
								{{localeEl.actions.select_all}}
							</b-form-checkbox>
						</button>
					</b-nav-text>
					<b-nav-text v-if="Object.keys(filterEl).length">
						<button
							:class="{ active: Object.keys(filtersLength).length }"
							:disabled="!Object.keys(filtersLength).length"
							class="btn btn-default"
							title="Clear Filters"
							@click="clearFilters()"
						>
							<b-icon-arrow-counterclockwise class="icon" />
							<span class="d-inline-block d-xl-none">Clear Filters</span>
						</button>
					</b-nav-text>

					<b-nav-text >
						<button
							class="btn btn-default active"
							title="Refresh"
							@click="$emit('refereshclicked')"
						>
							<b-icon icon="arrow-clockwise" class="icon"></b-icon>
							<span class="d-inline-block d-xl-none">Refresh</span>
						</button>
					</b-nav-text>

					<b-nav-text v-if="Object.keys(filterEl).length">
						<b-dropdown
							ref="filterDropdown"
							id="dropdown-form"
							right
							no-caret
							variant="link"
							@show="filterViewShow"
							@hide="onDropdownHide"
						>
							<template #button-content>
								<b-icon-funnel class="icon" title="Filters" />
								<b-badge variant="success" style="top:-8px">{{filtersLength.length}}</b-badge>
								<span class="d-inline-block d-xl-none" title="Filters">Filters</span>
							</template>
							<b-dropdown-form class="filter-list scroll-y">
								<FormElementWithValidation
									class="navbar-text"
									v-for="(item, index) in filterEl"
									:key="index"
									:item="item"
									v-model="filters[item.name]"
									:dataset="dataset[item.ds]"
									:locale="localeEl.fields"
									:filterView="true"
								>
								</FormElementWithValidation>
							</b-dropdown-form>
							<b-dropdown-divider></b-dropdown-divider>
							<div class="px-3 text-right">
								<b-button variant="secondary" size="sm" @click="filterViewHide()">
									<b-icon-x-circle /> Close</b-button
								>
								<b-button variant="danger" size="sm mx-3" @click="clearFilters()">
									<b-icon-arrow-counterclockwise /> Clear</b-button
								>
								<b-button variant="primary" size="sm" @click="applyFilter()">
									<b-icon-check-circle /> Apply</b-button
								>
							</div>
						</b-dropdown>
					</b-nav-text>
				
					<b-nav-text v-if="filterSetting.upload">
						<router-link :to="`${$route.path}/Upload`" class="btn btn-default active">
							<b-icon-upload class="icon" />
							<span class="d-inline-block d-xl-none">{{ localeEl.actions.upload }}</span>
						</router-link>
					</b-nav-text>
					
					<b-nav-text v-if="filterSetting.download" @click="download()">
						<button class="btn btn-default active" title="Download">
							<b-icon-download class="icon" />
							<span class="d-inline-block d-xl-none">Download</span>
						</button>
					</b-nav-text>

					<b-nav-text v-if="filterSetting.card">
						<router-link
							:to="changeView('card')"
							class="btn btn-default"
							:class="{ active: $route.query.view != 'grid' }"
							:title="cardViewConfig ? 'Card View' : 'Grid View'"
						>
							<b-icon-grid v-if="cardViewConfig" class="icon" />
							<b-icon-list v-else class="icon" />
							<span class="d-inline-block d-xl-none">Card View</span>
						</router-link>
					</b-nav-text>
					<b-nav-text v-if="filterSetting.grid">
						<router-link
							:to="changeView('grid')"
							class="btn btn-default"
							:class="{ active: $route.query.view == 'grid' }"
							:title="cardViewConfig ? 'List View' : 'Card View'"
						>
							<b-icon-list v-if="cardViewConfig" class="icon" />
							<b-icon-grid v-else class="icon" />
							<span class="d-inline-block d-xl-none">List View</span>
						</router-link>
					</b-nav-text>
					

					<slot name="body"></slot>
				</b-navbar-nav>
			</b-collapse>
		</b-navbar>
	</section>
</template>

<script>
import { mapState } from "vuex";

export default {
	props: ["dataSource", "dataset",  "filterEl", "filterSetting", "localeEl", "collection", "isCheckbox", "isAction", "PageTitle", "staticFilter", "additionalInfo", "cardViewConfig"],
	data: () => ({
		filters: {},
		filtersFormatted: [],
		selectAll: false,
		filterView:false,
		selectedFilters_copy:[],
		filtersLength: []
	}),
	computed: {
		...mapState(["selectedFilters"]),
	},
	watch: {
		/*dataset(newVal) {
			if (Object.keys(newVal).length) {
				this.applyFilter();
			}
		},*/
		filters: {
			handler(newval) {
				if (newval) {
					this.filterFormatting(newval)
				}
			},
			deep: true
		}
	},
	created(){
		this.filtersFormatted  =  this.selectedFilters
		this.selectedFilters_copy = [...this.selectedFilters]
		const _this = this

		/*if(this.localStorageFilter){
			const _filters = JSON.parse(this.localStorageFilter.getItem(`filters_${this.collection}`))
			this.$store.commit("OnSelectedFilters", _filters);
			this.filtersFormatted = _filters

			this.filtersFormatted.forEach(function(item){
				_this.filters[item.field] = item.value
			})
		}*/
	},
	methods: {
		filterFormatting(newval){
			// console.log(newval)
					this.filtersFormatted = [];
					this.filtersLength = [];
					for (var i in newval) {
						if(newval[i]){
							this.filtersLength.push(i)
							if(i.toLowerCase().includes('date') && Array.isArray(newval[i])){
								this.filtersFormatted.push({
									field: i,
									value: newval[i][0],
									asgn: "gteq",
									type: "DATE"
								},{
									field: i,
									value: newval[i][1],
									asgn: "lteq",
									type: "DATE"
								});
							}
							else {
								this.filtersFormatted.push({
									field: i,
									value: newval[i],
									asgn: "like",
									type: "string"
								});
							}
						}

					}
		},
		applyFilter() {
			console.log(this.filtersFormatted)
			this.filterFormatting(this.filters)
			this.$store.commit("OnSelectedFilters", this.filtersFormatted);
			//localStorage.setItem(`filters_${this.collection}`, JSON.stringify(this.filtersFormatted));
			this.filterViewHide();
		},
		clearFilters() {
			// localStorage.removeItem(`filters_${this.collection}`);
			this.filters = {}
			this.selectedFilters_copy.forEach(i=>{
				if(!this.filters[i.field] && i.value)
				{
					this.filters[i.field] = i.value
				}
				else{
					let temp = []
					temp =  this.filters[i.field]
					temp = [this.filters[i.field], i.value]
					this.filters[i.field] = temp
				}
			})
			this.$store.commit("OnSelectedFilters", this.selectedFilters_copy);
			this.filterFormatting(this.filters)
			this.filterViewHide();
		},
		/*closeFilters() {
			this.$refs.filterDropdown.hide(true);
		},*/
		download() {
			this.$emit("download");
		},
		changeView(view) {
			return {
				query: {
					...this.$route.query,
					view: view
				}
			};
		},
		selectAllCards(){
			if(this.selectAll == true){
				this.$store.commit("OnCheckedItem", this.dataSource);
			}
			else{
				this.$store.commit("OnCheckedItem", []);
			}
		},
		filterViewShow(){
			this.filterView = true
		},
		filterViewHide(){
			this.filterView = false
			this.$refs.filterDropdown.hide(true)
		},
		onDropdownHide(bvEvent){
			// this.filterView = false
			// this.$refs.filterDropdown.hide(true)
			if(this.filterView == true)
			bvEvent.preventDefault();
		}
	}
};
</script>

<style lang="scss" scoped>
.filterOverlay{position: fixed; left: 0; top:0; width: 100%; height: 100%; background: rgb(255 255 255 / 52%);  z-index: 3;}

</style>
