import io from "socket.io-client";

var onNotifyMsg;

const NC = function () {
    this._handlers = {};
    this._nsocket = (config.NSERVER && io(config.baseURL)) || {}
    const that = this;

    return {
        startSession: (userid, sessionid) => {
            this.userid = userid;
            this.sessionid = sessionid;

            this._nsocket.on(
                "connect",
                function () {
                    // console.log(this._nsocket,"==============");
                    "Connecting the N Center : " + that.userid + ":" + that.sessionid;
                    that._nsocket.emit("startsession", {
                        userid: that.userid,
                        sessionid: that.sessionid
                    });
                }.bind(this)
            );

            this._nsocket.on("session_validate", Obj => {
                // console.log("validarwe============");
                if (Obj.error) {
                    console.error(Obj.error);
                    that._nsocket.disconnect();
                    return;
                }
                console.log("Notification Center Connected.");
                //Receive the Collection change data
                that._nsocket.off("collectionChange");
                that._nsocket.on("collectionChange", vObj => {
                    that._handlers[vObj.collection](vObj.data, null);
                });
                that._nsocket.off("messages");
                //Receive the messages

                that._nsocket.on("messages", vObj => {
                    // alert("msg");

                    onNotifyMsg ? onNotifyMsg(vObj) : "";
                });
            });

            //Connection error handling
            this._nsocket.on(
                "collection_error",
                function (errObj) {
                    errObj.collection ?
                        this._handlers[errObj.collection](null, errObj.error) :
                        "";
                }.bind(this)
            );

            //Connection error handling
            this._nsocket.on("connect_error", function (err) {
                console.error("Error connecting notification server");
            });

            //Socket disconnect event
            this._nsocket.on("disconnect", function () {
                console.error("Disconnected from notification server");
            });
        },
        subMessages: callback => {
            onNotifyMsg = callback;
            this._nsocket.emit("offlineMsgs", {
                userid: this.userid,
                sessionid: this.sessionid
            });
            return;
        },
        unsubMessages: () => {
            onNotifyMsg = null;
            return;
        },
        subCollection: (collection, callback) => {
            if (!collection && !callback) {
                console.error("Insufficient data(collection/callback)");
                return;
            }
            this._handlers[collection] = callback;
            this._nsocket.emit("subCollection", {
                userid: this.userid,
                sessionid: this.sessionid,
                collection: collection
            });
        },
        unsubCollection: collection => {
            if (!collection) {
                console.error("Insufficient data(collection)");
                return;
            }
            this._nsocket.emit("unsubCollection", {
                userid: this.userid,
                sessionid: this.sessionid,
                collection: collection
            });
            delete this._handlers[collection];
        },
        stopSession: () => {
            this._nsocket.disconnect();
            this._nsocket = null;
        }
    };
};
const NC_Fallback = function () {
    return {
        startSession: (userid, sessionid) => {
            console.log('Notifications is disabled...')
        },
        subMessages: callback => {
        },
        unsubMessages: () => {
        },
        subCollection: (collection, callback) => {
        },
        unsubCollection: collection => {
        },
        stopSession: () => {
        }
    }
}
export default config.NSERVER ? NC : NC_Fallback;