<template>
	<div id="app">
		<Header />
		<!-- <router-view class name="header"></router-view> -->

		<router-view class></router-view>
		<!-- <router-view class name="footer"></router-view> -->

		<transition name="bounce" v-if="isLoading">
			<div class="loading">
				<!-- {{refCount}} -->
				<b-icon icon="three-dots" animation="cylon" font-scale="4"></b-icon>
			</div>
		</transition>

		<b-toast id="validation_toast" variant="danger">
			<template v-slot:toast-title>
				<div class="d-flex flex-grow-1 align-items-baseline">
					<b-img
						blank
						blank-color="#ff5555"
						class="mr-2"
						width="10"
						height="10"
					></b-img>
					<strong class="mr-auto">Required Fields</strong>
				</div>
			</template>
			<ul class="m-0 pl-4">
				<li v-for="(item, index) in validationErrors" :key="index">{{ item }}</li>
			</ul>
		</b-toast>
	</div>
</template>

<script>
import Header from "@/components/Header";
import { mapState } from "vuex";

export default {
	name: "app",
	components: {
		Header,
	},
	data: () => ({
		app_version: "ETF-V1-1.9",
	}),
	created() {
		console.log(
			"%c" + this.app_version,
			"color:blue;font-size: 14px;font-weight: 700"
		);
	},
	computed: {
		...mapState(["isLoading", "refCount", "validationErrors"]),
	},
};
</script>

